
.ptags{
  font-size: 18px;
  margin-top: 4px;
  margin-right: 8px;
  display: inline;
}

.calWidth {
  max-width: 400px;
}

#addBookingContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 260px;
}

.holder {
  width: 355px;
  padding-left: 20px;
}

#coolContainer {
  padding: 10px 10px 10px 10px;
  border-bottom: 1px solid gray;
  background-color: #f9e9f9;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#newContainer {
  border-bottom: 1px solid gray;
  background-color: #f9e9f9;
  position: relative;
  display: flex;
  flex-direction: column;
}


#divWhereWidthChanges {
  display: flex;
  flex-direction: column;
}

#bookingHolder {
  width: 375px;
  max-height: 100vh;
  overflow: auto;
  border-right: 1.5px solid black;
  display: inline-block;
}

.bookingHolderSubContainer {
  position: relative;
  top: 12px;
  height: 120px;
  width: 210px;
  border: 1px solid black;
  overflow: auto;
}

.itemPTag {
  padding: 1px 0px 1px 10px;
  cursor: pointer;
}

.itemPTag:hover {
  background-color: rgb(157, 217, 255);
}


@media (min-width: 1100px) {
    #newContainer {
      width: 100%;
    }
}


@media(max-width: 600px) {
  #bookingHolder {
    width: 100%;
    max-height: none;
    overflow: visible;
  }
  .calWidth {
    max-width: none;
    align-items: center;
  }
  #newContainer {
    align-items: center;
  }
}
